
import { useFormContext } from 'react-hook-form';
import React from 'react'
import CTAButton from './CTAButton';

const CustomForm = () => {

    return (
    <form className='w-full h-fit flex flex-col gap-9' method="post">
        <div className='w-[90%] h-fit flex flex-col gap-5 mx-auto'>
            <div className='w-full h-fit justify-center items-center flex gap-5 leading-5.5 text-sm text-[var(--richblack-5)] tracking-normal'>
                <label htmlFor="firstName" className='w-1/2 h-fit flex flex-col gap-1.5 '>
                    <p>
                        First Name
                    </p>
                    <input type="text" placeholder="Enter first name" id='firstName' name='firstName'
                    className="input input-neutral w-full h-fit p-3 rounded-lg shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset]
                    text-base leading-6 font-medium text-[var(--richblack-200)] bg-[var(--richblack-800)] focus:border-[var(--richblack-5)]" required/>  {/*input-error when error in form */}
                </label>

                <label htmlFor="lastName" className='w-1/2 h-fit flex flex-col gap-1.5'>
                    <p>
                        Last Name
                    </p>
                    <input type="text" placeholder="Enter last name" id='lastName' name='lastName'
                    className="input input-neutral w-full h-fit p-3 rounded-lg shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset]
                    text-base leading-6 font-medium text-[var(--richblack-200)] bg-[var(--richblack-800)] focus:border-[var(--richblack-5)]" required/>
                </label>
            </div>

            <label htmlFor="email" className='w-full h-fit flex flex-col gap-1.5'>
                <p className='leading-5.5 text-sm text-[var(--richblack-5)] tracking-normal'>
                    Email Address
                </p>
                <input type="email" placeholder="Enter email address" id='email' name='email' className="input input-neutral
                w-full h-fit p-3 rounded-lg shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset] focus:border-[var(--richblack-5)]
                text-base leading-6 font-medium text-[var(--richblack-200)] bg-[var(--richblack-800)]" required/>
            </label>

            <label htmlFor="phoneNumber" className='w-full h-fit flex flex-col gap-[20px]'>
                <p className='leading-5.5 text-sm text-[var(--richblack-5)] tracking-normal'>
                    Phone Number
                </p>
                {/* Country Code Drop Down */}
                <div className='w-full h-fit flex gap-5'>
                    <select tabIndex={0} className="w-[15%] h-fit shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset] focus:border-[var(--richblack-5)]
                    rounded-lg p-3 flex gap-3 bg-[var(--richblack-800)] cursor-pointer
                    text-base leading-6 font-medium text-[var(--richblack-200)]">
                        <option>+91</option>
                        <option>Item 2</option>
                    </select>
                    {/* Number Input Field */}
                    <input type="phone" placeholder="12345 67890" id='phoneNumber' name='phoneNumber'
                    className="input input-neutral w-[85%] h-fit p-3 rounded-lg shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset]
                text-base leading-6 font-medium text-[var(--richblack-200)] bg-[var(--richblack-800)] focus:border-[var(--richblack-5)]" required/>
                </div>
            </label>

        </div>

        <div className='w-[90%] h-[150px] mx-auto'>
            <label htmlFor="message" className='w-full h-full flex flex-col gap-1.5'>
                <p className='leading-5.5 text-sm w-full h-fit text-[var(--richblack-5)] tracking-normal'>
                Message
                </p>
                <textarea className="border-none w-full h-full shadow-[0px_-1px_0px_0px_#FFFFFF2E_inset] focus:border-[var(--richblack-5)]
                rounded-lg p-3 flex gap-3 bg-[var(--richblack-800)] text-base leading-6 font-medium text-[var(--richblack-200)] appearance-none" placeholder="Message" name="message" id="message"/>
            </label>
        </div>
        <CTAButton text="Send Message" active={true} className='w-[90%] mx-auto'/>
    </form>
  )
}

export default CustomForm;